import React, { useState, useEffect } from 'react';
import useService from '../useService';
import PieChart from './PieChart';
import Loader from './Loader';

const AppUsage = () => {
  const { sendDatatoServer } = useService();
  const [filter, setFilter] = useState('today');
  const [filteredData, setFilteredData] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchData();
  }, [filter]);

  const fetchData = async () => {
    setLoading(true); // Start loading
    try {
      const response = await sendDatatoServer('getAppUsage', { filter });
      setFilteredData(response.data);
    } catch (error) {
      console.error('Error fetching filtered data:', error);
    } finally {
      setLoading(false); // End loading
    }
  };

  const handleFilterChange = (selectedFilter) => {
    setFilter(selectedFilter);
  };

  const parsedDataPoints = filteredData?.dataPoints ? JSON.parse(filteredData.dataPoints) : [];

  return (
    <div className="card card-flush border-0">
      <div className="card-header pt-7">
        <h3 className="card-title align-items-start flex-column">
          <span className="card-label fs-3 fw-bold text-gray-800">App Usage</span>
        </h3>
        <div className="card-toolbar m-0">
          <ul className="nav nav-tabs nav-line-tabs nav-stretch fs-6 border-0 fw-bold date_filters" role="tablist">
            <li className="nav-item" role="presentation">
              <button className={`kt_activity_filter nav-link justify-content-center text-active-gray-800 ${filter === 'today' ? 'active' : ''}`} onClick={() => handleFilterChange('today')}>
                Today
              </button>
            </li>
            <li className="nav-item" role="presentation">
              <button className={`kt_activity_filter nav-link justify-content-center text-active-gray-800 ${filter === 'week' ? 'active' : ''}`} onClick={() => handleFilterChange('week')}>
                Week
              </button>
            </li>
            <li className="nav-item" role="presentation">
              <button className={`kt_activity_filter nav-link justify-content-center text-active-gray-800 ${filter === 'month' ? 'active' : ''}`} onClick={() => handleFilterChange('month')}>
                Month
              </button>
            </li>
            <li className="nav-item" role="presentation">
              <button className={`kt_activity_filter nav-link justify-content-center text-active-gray-800 ${filter === 'year' ? 'active' : ''}`} onClick={() => handleFilterChange('year')}>
                Year
              </button>
            </li>
          </ul>
        </div>
      </div>
      {loading ? (
        <div className="card-body pt-6"><Loader /></div>
      ) : (
        <PieChart dataPoints={parsedDataPoints} />
      )}
    </div>
  );
};

export default AppUsage;