import React, { useState, useEffect, useCallback } from 'react';
import useService from '../../useService';

const ProjectDetail = () => {
  const { sendDatatoServer } = useService();

  const [projectName, setProjectName] = useState('');
  const [taskDetails, setTaskDetails] = useState('');
  const [projects, setProjects] = useState([{ id: 0, name: 'Default Project' }]);

  useEffect(() => {
    window.api.send('get-cached-project-details');
    window.api.receive('project-detail', getProjectDetail);
    fetchProjects();

    return () => {
      // Clean up event listener when the component unmounts
      window.api.remove('get-cached-project-details');
      window.api.remove('project-detail');
    };
  }, []);

  const fetchProjects = async () => {
    const projectsFromAPI = [];
    try {
      const response = await sendDatatoServer('projects', {});
      if (response.status) {
        response.data.forEach(data => {
          const { project: { id: projectId, project_title: projectTitle } } = data;
          projectsFromAPI.push({ id: projectId, name: projectTitle });
        });
      } else {
        // Handle the error case
      }
    } catch (error) {
      console.error('Error:', error);
    }

    setProjects(projectsFromAPI);
  };

  const getProjectDetail = (data) => {
    if (data?.projectDetail) {
      const { projectName, taskDetails } = data.projectDetail;
      setProjectName(projectName || '');
      setTaskDetails(taskDetails || '');
    }

    const cachedProjectDetails = localStorage.getItem('projectDetails');
    if (cachedProjectDetails && projectName === '') {
      const parsedProjectDetails = JSON.parse(cachedProjectDetails);
      setProjectName(parsedProjectDetails.projectName);
      setTaskDetails(parsedProjectDetails.taskDetails);
      window.api.send('project-details-submitted', parsedProjectDetails);
    }
  }

  const handleSubmit = () => {
    const projectDetails = { projectName: projectName, taskDetails: taskDetails };
    window.api.send('project-details-submitted', projectDetails);

    localStorage.setItem('projectDetails', JSON.stringify(projectDetails));
  };

  // Debounce function
  const debounce = (func, delay) => {
    let debounceTimer;
    return function(...args) {
      const context = this;
      clearTimeout(debounceTimer);
      debounceTimer = setTimeout(() => func.apply(context, args), delay);
    };
  };

  // Create a debounced version of handleSubmit
  const debouncedSubmit = useCallback(debounce(handleSubmit, 1000), [projectName, taskDetails]);

  const handleTaskDetailsChange = (event) => {
    setTaskDetails(event.target.value);
    debouncedSubmit();
  };

  return (
    <form onSubmit={(e) => e.preventDefault()}>
      <div className="d-flex d-grid mt-6 gap-2">
        <select
          name="projectName"
          id="projectName"
          className="form-control"
          value={projectName}
          onChange={(e) => setProjectName(e.target.value)}
          style={{ height: "40px" }}
        >
          <option value="">Select Project</option>
          {projects.map((project) => (
            <option key={project.id} value={project.id}>
              {project.name}
            </option>
          ))}
        </select>
      </div>
      <div className="d-flex d-grid mt-2 gap-2">
        <input
          type="text"
          className="form-control"
          id="taskDetails"
          name="taskDetails"
          value={taskDetails}
          onChange={handleTaskDetailsChange}
          placeholder='Enter one liner task detail'
        />
      </div>
    </form>
  );
};

export default ProjectDetail;