import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

function AppNavbar() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [userName, setUserName] = useState('');
  const [userEmail, setUserEmail] = useState('');
  const navigate = useNavigate();

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  useEffect(() => {
    const user = localStorage.getItem('user');
    if(user) {
      const userDetails = JSON.parse(user);
      setUserName(userDetails.name);
      setUserEmail(userDetails.email);
    }
  }, []);

  const handleLogout = async () => {
    window.api.send('authenticate',true)
    window.api.send('logout',true)
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    navigate('/');
  };

  const menuStyle = {
    zIndex: 107,
    position: 'fixed',
    transform: 'translate(-560px, 110px)',
    inset: '0 0 auto auto',
    margin: '0',
    right: '-550px'
  };

  return (
    <div className="app-navbar flex-shrink-0">
      <div className="app-navbar-item" id="kt_header_user_menu_toggle" onClick={toggleMenu}>
        <div className="d-flex align-items-center border border-dashed border-gray-300 rounded p-2" data-kt-menu-trigger="{default: 'click', lg: 'hover'}" data-kt-menu-attach="parent" data-kt-menu-placement="bottom-end">
          <div className="cursor-pointer symbol me-3 symbol-35px symbol-lg-45px">
            <img src={'assets/media/avatars/300-1.jpg'} alt="user" />
          </div>
          <div className="me-4">
            <a href="#" className="text-gray-900 text-hover-primary fs-6 fw-bold">{userName}</a>
            <a href="#" className="text-gray-500 text-hover-primary fs-7 fw-bold d-block">{userEmail}</a>
          </div>
          <i className="ki-outline ki-down fs-2 text-gray-500 pt-1"></i>
        </div>
        <div className={`menu menu-sub menu-sub-dropdown menu-column menu-rounded menu-gray-800 menu-state-bg menu-state-color fw-semibold py-4 fs-6 w-275px ${isMenuOpen ? 'show' : ''}`} style={isMenuOpen?menuStyle:{}}>
        <div className="menu-item px-3">
            <div className="menu-content d-flex align-items-center px-3">
            <div className="symbol symbol-50px me-5">
                <img alt="profile-pic" src={'assets/media/avatars/300-1.jpg'} />
            </div>
            <div className="d-flex flex-column">
                <div className="fw-bold d-flex align-items-center fs-5">{userName}
                <span className="badge badge-light-success fw-bold fs-8 px-2 py-1 ms-2">Pro</span></div>
                <a href="#" className="fw-semibold text-muted text-hover-primary fs-7">{userEmail}</a>
            </div>
            </div>
        </div>
        <div className="separator my-2"></div>
            <div className="menu-item px-5">
                <a href="#" className="menu-link px-5" onClick={handleLogout}>Sign Out</a>
            </div>
        </div>
      </div>
    </div>
  );
}

export default AppNavbar;