import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import 'font-awesome/css/font-awesome.min.css';

const Login = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
    const navigate = useNavigate();

    // Check if token and user information are already present in localStorage
    useEffect(() => {
        const token = localStorage.getItem('token');
        const user = localStorage.getItem('user');
        if (token && user) {
            // Redirect to dashboard page
            navigate('/dashboard');
        }
    }, [navigate]);

    const handleFormSubmit = async (e) => {
        e.preventDefault();
        const is_tracking = 1;
        const apiUrl = process.env.REACT_APP_LOGIN_URL;

        if (!/\S+@\S+\.\S+/.test(email)) {
            setError('Please enter a valid email address.');
            return;
        }

        // Simple password validation
        if (password.length < 5) {
            setError('Password must be at least 5 characters long.');
            return;
        }

        try {
            const response = await fetch(apiUrl, {
              method: 'POST',
              headers: { 'Content-Type': 'application/json','Access-Control-Allow-Origin':'*' },
              body: JSON.stringify({ email, password, is_tracking }),
            });
      
            if (response.ok) {
              const data = await response.json();
              const token = data.data.token;
              const user = data.data.user;
              window.api.send('logout',true);  
              console.log('Login successful:', data);
              // Save token and user info to localStorage or session storage
              localStorage.setItem('token', token);
              localStorage.setItem('user', JSON.stringify(user));
              window.api.send('userInfo', user);
			  window.api.send('saveToken', token);
              // Redirect to dashboard page
              navigate('/dashboard');
            } else {
              const errorResponse = await response.json();
              setError(errorResponse.error);
              console.error('Login failed:', errorResponse);
            }
          } catch (error) {
            console.error('Error during fetch:', error);
            // Handle network error here
          }
    };

    return (
        <div className="d-flex flex-column flex-root">
            <div className="d-flex flex-column flex-lg-row flex-column-fluid">
                <div className="d-flex flex-column flex-lg-row-auto w-xl-600px positon-xl-relative h-xl-900px"
                    style={{backgroundColor: '#dddddd'}}>
                    <div className="d-flex flex-column position-xl-fixed top-0 bottom-0 w-xl-600px h-xl-900px">
                        <div className="d-flex flex-center flex-column text-center flex-column-fluid">
                            <a href="#" className="py-9 mb-5">
                                <img alt="Logo" src={'assets/media/main_logo.58c7ac2.svg'} className="h-90px" />
                            </a>
                            <h1 className="fw-bolder fs-2qx pb-5 pb-md-10" style={{color: '#986923'}}>Welcome to WindShieldHub</h1>
                            <p className="fw-bold fs-2" style={{color: '#986923'}}>Your local source for <br />auto glass replacement
                            </p>
                        </div>
                    </div>
                </div>
                <div className="d-flex flex-column flex-lg-row-fluid py-10">
                    <div className="d-flex flex-center flex-column flex-column-fluid">
                        <div className="w-lg-500px p-10 p-lg-15 mx-auto">
                        
                            <form className="form w-100" id="wsh_login" onSubmit={handleFormSubmit}>
                                <div className="text-center mb-10">
                                    <h1 className="text-dark mb-3">Sign In</h1>
                                </div>
                                <div className="fv-row mb-10">
                                    <label className="form-label fs-6 fw-bolder text-dark">Email</label>
                                    <input className="form-control form-control-lg form-control-solid" type="email" name="email"
                                        autoComplete="off" value={email} onChange={(e) => setEmail(e.target.value)} required="required"/>
                                </div>
                                <div className="fv-row mb-10">
                                    <div className="d-flex flex-stack mb-2">
                                        <label className="form-label fw-bolder text-dark fs-6 mb-0">Password</label>
                                        <a href="#"
                                            className="link-primary fs-6 fw-bolder">Forgot Password ?</a>
                                    </div>
                                    <input className="form-control form-control-lg form-control-solid" type="password"
                                        name="password" required="required" autoComplete="off" value={password}
                                        onChange={(e) => setPassword(e.target.value)}/>
                                </div>
                                <div className="text-center">
                                    <button type="submit" id="kt_sign_in_submit" className="btn btn-lg btn-primary w-100 mb-5">
                                        <span className="indicator-label">Continue</span>
                                        {!error && <span className="indicator-progress">Please wait...
                                            <span className="spinner-border spinner-border-sm align-middle ms-2"></span></span> }
                                    </button>
                                </div>
                            </form>
                            {error && <div className="text-danger">{error}</div>}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
  };
  
export default Login;