import React, { useState, useEffect } from 'react';
import Modal from 'react-modal';
import useService from '../useService';
import Loader from './Loader';

Modal.setAppElement('#root');

const ImageModal = ({ isOpen, onRequestClose, imageSrc }) => {
  const { sendDatatoServer } = useService();
  const [loading, setLoading] = useState(true);
  const [imageUrl, setImageUrl] = useState('');

  useEffect(() => {
    if (imageSrc) {
      getImageUrl(imageSrc);
    }
  }, [imageSrc]);

  const getImageUrl = async (imageSrc) => {
    setLoading(true);
    try {
      const response = await sendDatatoServer('getImageUrl', { url: imageSrc });
      setImageUrl(response.url);
    } catch (error) {
      console.log('Error fetching image data:', error);
      setImageUrl('');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Modal
      isOpen={isOpen}
      onRequestClose={onRequestClose}
      style={{
        content: {
          top: '50%',
          left: '50%',
          width: "100%",
          height: "100%",
          right: 'auto',
          bottom: 'auto',
          marginRight: '-50%',
          transform: 'translate(-50%, -50%)',
          zIndex: 9999,
          paddingTop: '100px',
          border: 'none',
          background: 'none',
        },
        overlay: {
          zIndex: 999,
          backgroundColor: 'rgba(0, 0, 0, 0.75)',
        },
      }}
    >
      <div className="imageContainer">
        <span className="close" onClick={onRequestClose} style={{ cursor: 'pointer', fontSize: '24px', position: 'absolute', top: '10px', right: '20px', color: 'white' }}>&times;</span>
        {loading ? (
          <Loader />
        ) : (
          <img src={imageUrl} alt="screenshot" className="zoom-in" id="img01" />
        )}
      </div>
    </Modal>
  );
};

export default ImageModal;