import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import useService from '../../useService';

const Tray = () => {
    const navigate = useNavigate();
    const { sendDatatoServer } = useService();
    const [userData, setUserData] = useState({});
    const [workedTime, setWorkedTime] = useState(0);
    const { checkedIn, checkedOut, totalBreaks } = userData;
    const [breakTime, setBreakTime] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [isActive, setIsActive] = useState(false);

    const getUserDetail = async () => {
        setLoading(true);
        try {
            const response = await sendDatatoServer('getUserDetail', {});
            setUserData(response.data);
            setBreakTime(parseInt(totalBreaks?.total_break ?? 0));
            setIsActive(response.data.isBreakOn);
            window.api.send('userState', response.data);
        } catch (error) {
            setError(error);
            console.log('Error:', error);
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        let storedToken = localStorage.getItem('token');

        if (!storedToken) {
            navigate(0);
        } else {
            getUserDetail();
            const intervalId = setInterval(getUserDetail, 60000); // Call getUserDetail every 1 minutes

            window.api.receive('started_working', function(e) {
                getUserDetail();
            });

            window.api.receive('getUserDetail', function(e) {
                getUserDetail();
            });

            window.api.receive('started_break', function(e) {
                sendDatatoServer('startBreak', { 'type': 5 }).then(response => {
                    getUserDetail();
                });
            });

            return () => {
                // Clean up event listener when component unmounts
                window.api.remove('started_working');
                window.api.remove('getUserDetail');
                window.api.remove('started_break');
                clearInterval(intervalId);
            };
        }
    }, [isActive]);

    useEffect(() => {
        let intervalId;

        if (checkedOut) {
            const startTime = checkedOut.time_worked;
            setWorkedTime(startTime);
        } else if (checkedIn) {
            const startTime = checkedIn.time_worked;
            setWorkedTime(startTime);
            intervalId = setInterval(() => {
                setWorkedTime(prevTime => prevTime + 1);
            }, 1000); // Update every second
        }

        return () => clearInterval(intervalId);
    }, [checkedIn, checkedOut]);

    useEffect(() => {
        let breakIntervalId;
        if (checkedOut) {
            // No need to start interval if checked out
            const startTime = parseInt(totalBreaks?.total_break ?? 0);
            setBreakTime(startTime);
        } else if (totalBreaks) {
            const startTime = parseInt(totalBreaks?.total_break ?? 0);
            setBreakTime(startTime);
            breakIntervalId = setInterval(() => {
                setBreakTime(prevTime => prevTime + 1);
            }, 1000); // Update every second
        }

        return () => clearInterval(breakIntervalId);
    }, [totalBreaks]);

    const toggleActive = async () => {
        setLoading(true);
        if (checkedOut) {
            window.api.send('checked_out');
            return false;
        }
        setIsActive(!isActive);
        const storedToken = localStorage.getItem('token');
        if (!isActive) {
            await window.api.send('start_break', { token: storedToken });
        } else {
            await window.api.send('authenticate', true);
            await window.api.send('start_working', true);
        }
        setLoading(false);
    };

    const convertSecondsToHoursMinutes = (seconds) => {
        const hours = Math.floor(seconds / 3600);
        const minutes = Math.floor((seconds % 3600) / 60);
        const remainingSeconds = seconds % 60;
        return `${hours}h ${minutes}m ${remainingSeconds}s`;
    };

    return (
        <div className="container" style={{ width: '150px', height: '180px' }}>
            <div className={`rounded-circle bg-${isActive ? 'danger' : 'success'}`} style={{ width: '150px', height: '150px', margin: 'auto', marginTop: '75px', display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'pointer' }} onClick={toggleActive}>
                <button className="btn" style={{ width: '100px', height: '100px', padding: 0 }}>
                    <div className="text-center">
                        <span style={{ fontSize: '20px', color: '#fff' }}>{isActive ? convertSecondsToHoursMinutes(breakTime) : convertSecondsToHoursMinutes(workedTime)}</span><br />
                        <span style={{ fontSize: '12px', color: '#fff' }}>{isActive ? 'Break Time' : 'Worked Time'}</span>
                    </div>
                </button>
            </div>
        </div>
    );
};

export default Tray;