import React, { useState, useEffect } from 'react';
import useService from '../useService';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import ImageModal from './ImageModal';
import Loader from './Loader';

const Screenshots = () => {
  const { sendDatatoServer } = useService();
  const [screenshotsData, setScreenshotsData] = useState([]);
  const [loading, setLoading] = useState(true);
  
  const currentDateFormatted = () => {
    const today = new Date();
    const year = today.getFullYear();
    const month = (today.getMonth() + 1).toString().padStart(2, '0');
    const day = today.getDate().toString().padStart(2, '0');
    return `${year}-${month}-${day}`;
  };

  const [currentPage, setCurrentPage] = useState(1);
  const [currentDate, setCurrentDate] = useState(currentDateFormatted());
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedImage, setSelectedImage] = useState(null);

  useEffect(() => {
    fetchScreenshotsData();
  }, [currentPage, currentDate]);

  const fetchScreenshotsData = async () => {
    try {
      setLoading(true);
      let apiUrl = `getScreenshot?filter_date=${currentDate}`;
      if (currentPage !== 1) {
        apiUrl += `&page=${currentPage}`;
      }
      const response = await sendDatatoServer(apiUrl, {});
      setScreenshotsData(response.data);
    } catch (error) {
      console.error('Error fetching screenshots:', error);
    } finally {
      setLoading(false);
    }
  };

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const handleDelete = (id, time_spent) => {
    const minutes = time_spent / 60;
    if (window.confirm(`If you delete this your work of ${minutes} minutes will be removed.`)) { 
      sendDatatoServer('deleteScreenshot', { id }).then(() => {
        fetchScreenshotsData();
      });
    }
  };

  const handleDateChange = (date) => {
    const formattedDate = date.toISOString().split('T')[0];
    setCurrentDate(formattedDate);
  };

  const handleRefresh = () => {
    fetchScreenshotsData();
  };

  const handleImageClick = (imageSrc) => {
    setSelectedImage(imageSrc);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedImage(null);
  };

  const renderPagination = () => {
    const pages = [];

    if (screenshotsData?.screenshots?.last_page > 1) {
      for (let i = 1; i <= screenshotsData.screenshots.last_page; i++) {
        pages.push(
          <li key={i} className={`page-item ${i === currentPage ? 'active' : ''}`}>
            <button className="page-link" onClick={() => handlePageChange(i)}>{i}</button>
          </li>
        );
      }
    }
  
    return (
      <div id="pagination">
        <nav aria-label="Page navigation">
          <ul className="pagination">
            {pages}
          </ul>
        </nav>
      </div>
    );
  };

  return (
    <div className="card card-flush">
      <div className="card-header pt-7 mb-2">
        <h3 className="card-title align-items-start flex-column">
          <span className="card-label fw-bold text-gray-800">Screenshot</span>
          <span className="text-gray-500 mt-1 fw-semibold fs-6">Let’s see what have you been upto</span>
        </h3>
      </div>
      <div className="card-body text-center">
        <div className="row g-6 g-xl-9 mb-6 mb-xl-9 screenshot-container">
          <div className="card-header card-header-stretch">
            <div className="card-title d-flex align-items-center">
              <i className="ki-outline ki-calendar-8 fs-1 text-primary me-3 lh-0"></i>
              <h3 className="fw-bold m-0 text-gray-800">{currentDate}</h3>
            </div>
            <div className="card-toolbar m-0">
              <ul className="card-title d-flex align-items-center">
                <DatePicker
                  selected={new Date(currentDate)}
                  onChange={date => handleDateChange(date)}
                  className="form-control"
                  placeholderText="Pick a date"
                />
              </ul>
              <button className="btn btn-success btn-icon btn-light btn-sm mx-2 mt-5" onClick={handleRefresh}>
                <i className="fas fa-sync-alt"></i>
              </button>
            </div>
          </div>

          {loading ? (
            <Loader />
          ) : (
            <>
              {renderPagination()}
              {/* Check if screenshots data exists */}
              {screenshotsData && screenshotsData.screenshots && screenshotsData.screenshots.data.length > 0 ? (
                screenshotsData.screenshots.data.map(screenshot => (
                  <div className="col-auto" key={screenshot.id}>
                    <div className="card h-100">
                      <div className="card-body d-flex justify-content-center text-center flex-column screenshot-card p-5">
                        <div className="text-gray-800 text-hover-primary d-flex flex-column">
                          <a href="#" className="delete_image" onClick={() => handleDelete(screenshot.id, screenshot.time_spent)}>
                            <i className="fa fa-trash" style={{ position: 'absolute', top: 0, right: 0, margin: '10px' }}></i>
                          </a>
                          <div className="flex">
                            {/* Assuming each screenshot can have multiple images */}
                            {screenshot.screenshots.map(scr => (
                              <div className="symbol symbol-md-100px symbol-2by3 mb-5" key={scr.id} onClick={() => handleImageClick(scr.file_path)} >
                                <img src={scr.thumbnail} data-url={scr.file_path} id="screenshot" alt="screenshot" />
                              </div>
                            ))}
                          </div>
                          <div className="fs-5 fw-bolder mb-2">{new Date(screenshot.created_at).toLocaleString()}</div>
                        </div>
                        <div className="fs-7 fw-bold text-gray-400">{Math.round((screenshot.time_spent / 60 + Number.EPSILON) * 100) / 100} Minutes</div>
                      </div>
                    </div>
                  </div>
                ))
              ) : (
                <div className="card-body text-center">
                  {/* Render empty state */}
                  <img className="h-250px mb-8 theme-light-show" src={'assets/media/illustrations/sigma-1/21.png'} alt="" />
                  <img className="h-250px mb-8 theme-dark-show" src={'assets/media/illustrations/sigma-1/21.png'} alt="" />
                  <h1 className="fw-semibold text-gray-600 mb-lg-12">No Screenshot found</h1>
                </div>
              )}
            </>
          )}
        </div>
      </div>
      {/* Modal for showing the clicked image */}
      {selectedImage && (
        <ImageModal
          isOpen={isModalOpen}
          onRequestClose={closeModal}
          imageSrc={selectedImage}
        />
      )}
    </div>
  );
};

export default Screenshots;