import { useEffect, useRef, useState, useCallback } from 'react';

function useWebSocketService(userId = 0) {
  const [webSocketConnection, setWebSocketConnection] = useState(null);
  const pingIntervalRef = useRef(null);

  useEffect(() => {
    if (userId > 0) {
      const socketUrl = `wss://socket.windshieldhub.com?user_id=${userId}`;
      const ws = new WebSocket(socketUrl);

      ws.onopen = (event) => {
        console.log('WebSocket connection opened');
        // Send ping frame every 30 seconds
        pingIntervalRef.current = setInterval(() => {
          if (ws.readyState === WebSocket.OPEN) {
            ws.send(JSON.stringify({ ping: 'ping' }));
          }
        }, 30000);
      };

      ws.onmessage = (event) => {
        console.log('WebSocket message received:', event.data);
      };

      ws.onerror = (event) => {
        console.log('WebSocket error');
      };

      ws.onclose = (event) => {
        console.log('WebSocket connection closed');
        if (pingIntervalRef.current) {
          clearInterval(pingIntervalRef.current);
        }
      };

      ws.ondisconnect = (event) => {
        console.log('WebSocket ondisconnect');
      };

      setWebSocketConnection(ws);

      // Cleanup function to close WebSocket and clear interval on unmount
      return () => {
        if (ws) {
          ws.close();
        }
        if (pingIntervalRef.current) {
          clearInterval(pingIntervalRef.current);
        }
      };
    }
  }, [userId]);

  const sendMessage = useCallback((message) => {
    if (webSocketConnection && webSocketConnection.readyState === WebSocket.OPEN) {
      webSocketConnection.send(JSON.stringify(message));
    } else {
      console.log('WebSocket is not open. Message not sent:', message);
    }
  }, [webSocketConnection]);

  return { webSocketConnection, sendMessage };
}

export default useWebSocketService;