import React, { useEffect, useState } from 'react';
import Stats from './Stats';
import Screenshots from './Screenshots';
import useService from '../useService';
import useWebSocketService from '../useWebSocketService';
import AppUsage from './AppUsage';
import Loader from './Loader';
import Quote from './Quote';
import ProjectDetail from './Pages/ProjectDetail';
import { useNavigate } from 'react-router-dom';

const UserData = ({ token }) => {
  
  const {postRequest, base64ToBlob, sendDatatoServer} = useService();
  const [userData, setUserData] = useState(null);
  const [userId, setUserId] = useState(0);
  const { webSocketConnection, sendMessage } = useWebSocketService(userId);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [breakOn, setBreakOn] = useState(false);
  const [showStartBreak, setShowStartBreak] = useState(false);
  const [showStartWorking, setShowStartWorking] = useState(false);
  const [checkedIn, setCheckedIn] = useState(null);
  const [checkedOut, setCheckedOut] = useState(null);
  const [loggedIn, setLoggedIn] = useState(false);
  const navigate = useNavigate();

  const handleLogout = async () => {
    window.api.send('authenticate',true)
    window.api.send('logout',true)
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    navigate('/');
  };

  const getUserDetail = async () => {
    setLoading(true);
    try {
      const response = await sendDatatoServer('getUserDetail', {});
      // console.log('Fetched data:', response.data);
      setUserId(response.data.user.user_id);
			// window.api.send('userInfo',response.data.user);
      setUserData(response.data);
      setCheckedIn((response.data.checkedIn) ? response.data.checkedIn.checkin_time : 0);
      setCheckedOut(response.data.checkedOut)
      window.api.send('userState', response.data);
      setShowStartBreak(!response.data.isBreakOn && response.data.checkedIn);
      setBreakOn(response.data.isBreakOn);
      setShowStartWorking(response.data.isBreakOn && !response.data.today);
      if(!response.data.isBreakOn && response.data.checkedIn){
        // console.log('checkedIn',response.data.checkedIn);
        window.api.send('startTracking', true);
      }
    } catch (error) {
      setError(error);
      console.error('Error:', error);
      handleLogout();
    } finally {
      setLoading(false);
    }
  };

    const handleCheckIn = async () => {
        try {
            const response = await sendDatatoServer('makeCheckin', {});
            if(response.status){
                getUserDetail();
            } else {
              window.api.send('notify',{title:'Checkin Error',msg:response.msg})
            }
        } catch (error) {
            console.error('Error:', error);
            window.api.send('notify',{title:'Checkin Error',msg:error})
        }
    };

    const handleClockOut = async () => {
        try {
            const response = await sendDatatoServer('makeCheckout', {});
            getUserDetail();
            window.api.send('clockout',{token:token});
        } catch (error) {
            console.error('Error:', error);
        }
    };

    const handleSync = async () => {
        getUserDetail();
    };

    const handleStartBreak = async () => {
      // console.log('start break console');
      window.api.send('start_break',{token:token});
    };

    const handleStartWorking = async () => {
      window.api.send('authenticate',true)
			window.api.send('start_working',60000)
    };

  useEffect(() => {
    getUserDetail();
    const intervalId = setInterval(getUserDetail, 300000); //after every 2.5 mins

    window.api.receive('started_working',function(e){
			getUserDetail();
		})

    window.api.receive('sync',function(e){ getUserDetail(); })
    window.api.receive('getUserDetail',function(e){ getUserDetail(); })

    window.api.receive('user-loggedIn', userLoggedIn);
    
		window.api.receive('started_break',function(e){
      sendDatatoServer('startBreak',{'type':5}).then(response => {
				getUserDetail();
        if (webSocketConnection && webSocketConnection.readyState === WebSocket.OPEN) {
          
        }
			});
		});

    // Attach event listener for request-data-from-renderer event
    window.api.receive('request-data-from-renderer', handleDataFromRenderer);

    return () => {
      // Clean up event listener when component unmounts
      window.api.remove('started_working');
      window.api.remove('getUserDetail');
      window.api.remove('user-loggedIn');
      window.api.remove('started_break');
      window.api.remove('request-data-from-renderer');
      clearInterval(intervalId);
    };

  }, []);

  useEffect(() => {
    if (loggedIn) {
      // console.log('checkedIn',checkedIn);
      if(checkedIn !== 0 && checkedIn !== null  && checkedOut === null && !breakOn) {
        window.api.send('startTracking', true);
        setLoggedIn(false);
      }
    }
  }, [loggedIn,checkedIn,checkedOut,breakOn]);

    const userLoggedIn = (data) => {
      setLoggedIn(true);
    }

    const handleDataFromRenderer = (data) => {
      // console.log('request-data-from-renderer', data);
      if (data.type === 'saveImage') {
          const screenshot2Blob = base64ToBlob(data.body, 'image/png');
          const formData = new FormData();
          formData.append('image', screenshot2Blob);
          formData.append('tracker_id', data.trackId);
          postRequest(process.env.REACT_APP_BASE_URL + data.url, formData)
              .then(data => { console.log('started_working'); })
              .catch(error => { console.error('Error:', error); });
      } else if (data.type === 'trackTime') {
          sendDatatoServer(data.url,data.body).then(response => {
            // console.log(response,'timeresponse');
            window.api.send('saveScreenshot', response.data.track_id);
          })
      } else if (data.type === 'trackApplications') {
          sendDatatoServer(data.url,data.body).then(response => {
            // console.log(response,'trackApplications');
          })
      }

      if (data.type === 'startBreak') {
          window.api.send('start_break', { token: token });
      }
      if (data.type === 'NotifyBreakOn') {
          if (webSocketConnection && webSocketConnection.readyState === WebSocket.OPEN) {
              sendMessage({
                task_type: 'break_on',
                type: 'SEND_TO_USER',
                user_id: userId,
                message: 'Your break is on. Turn off break to start working again.'
              });
          }
      }
      if (data.type === 'stopBreak') {
          const response = sendDatatoServer('stopBreak', {}).then(response => {
            getUserDetail();
            if (webSocketConnection && webSocketConnection.readyState === WebSocket.OPEN) {
                sendMessage({
                  task_type: 'break_off',
                  type: 'SEND_TO_USER',
                  user_id: userId,
                  message: 'Break turned off.'
                });
            }
          });
      }
    };

  return (
    <>
      {loading && <div><Loader /></div>}
      
      {error && <div>Error: {error.message}</div>}

      {userData && 
        <div>
          <div className="row g-5 g-xxl-10">
            <div className="col-md-8 mb-5 mb-xl-10">
              <div className="card border-0 mb-5 mb-xl-11" data-bs-theme="light" style={{ backgroundColor: showStartBreak ? '#1a99ce' : "#676767"  }}>
                <div className="card-body py-0">
                  <div className="row align-items-center lh-1 h-100">
                    <div className="col-7 ps-xl-10 pe-5 ">
                      <Quote />
                      <div className="d-flex align-items-center flex-wrap d-grid gap-2 mb-9">
                        <div className="d-flex align-items-center me-5 me-xl-13"></div>
                        <div className="d-flex align-items-center"></div>
                      </div>
                      <div className="d-flex d-grid gap-2">
                        <button className="btn btn-sm btn-warning me-2" onClick={handleSync}>Synchronize</button>
                        {userData.checkedIn ? (
                            <>
                              {showStartBreak && (
                                <button className="btn btn-sm btn-light me-2" onClick={handleStartBreak}>
                                  Start Break
                                </button>
                              )}
                              {(showStartWorking || breakOn) ? (
                                <button className="btn btn-sm btn-primary me-2" onClick={handleStartWorking}>
                                  Stop Break
                                </button>
                              ) : (
                                <button className="btn btn-sm btn-primary me-2" onClick={handleClockOut}>
                                  Clock Out
                                </button>
                              )}
                            </>
                          ) : (
                            <button className="btn btn-sm btn-primary me-2" onClick={handleCheckIn}>
                              Clock In
                            </button>
                          )}
                      </div>
                      <ProjectDetail />
                    </div>
                    <div className="col-5 pt-5 pt-lg-15">
                      <div className="bgi-no-repeat bgi-size-contain bgi-position-x-end bgi-position-y-bottom h-325px" style={{ backgroundImage: "url('assets/media/svg/illustrations/easy/8.svg')" }}></div>
                    </div>
                  </div>
                </div>
              </div>
              <AppUsage />
            </div>
            <Stats data={{userData,breakOn}} />
          </div>
          <Screenshots />
        
        </div>
      }  

    </>
  );
};

export default UserData;