import React, { useState, useEffect } from 'react';
import useService from '../useService';

const Timeline = () => {
  const { sendDatatoServer } = useService();
  const [timeline, setTimeline] = useState([]);

  // Define fetchData outside of useEffect
  const fetchData = async () => {
    try {
      const response = await sendDatatoServer('getTimeLine', {});
      setTimeline(response.data.timeline);
    } catch (error) {
      console.error('Error fetching timeline data:', error);
    }
  };

  // useEffect with an empty dependency array to run only once
  useEffect(() => {
    fetchData();
  }, []); // Only run once on mount

  return (
    <div className="card border-0">
      <div className="card-body py-12">
        <button className="btn btn-light-warning fs-3 fw-bolder w-100 py-5 mb-13">TimeLine</button>
        <div className="d-flex border border-primary border-dashed rounded p-5 bg-light-primary mb-6" style={{ overflowY: 'scroll', maxHeight: '230px' }}>
          <span className="me-5"></span>
          <div className="timeline-label">
            {timeline && timeline.map((time, index) => (
              (time.timestamp > 0) && (
                <div className="timeline-item" key={index}>
                  <div className="timeline-label fw-bold text-gray-800 fs-6">
                    {time?.timestamp > 0 && new Date(time.timestamp * 1000).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                  </div>
                  <div className="timeline-badge">
                    <i className={`fa fa-genderless ${time.event === 'work_start' ? 'text-success' : time.event === 'break_end' ? 'text-warning' : 'text-danger'} fs-1`}></i>
                  </div>
                  <div className="fw-normal timeline-content text-muted ps-3">
                    {time.event}
                  </div>
                </div>
              )
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Timeline;