import { useLayoutEffect } from 'react';
import * as am5 from "@amcharts/amcharts5";
import * as am5percent from "@amcharts/amcharts5/percent";

function PieChart(props) {
  useLayoutEffect(() => {
    let root = am5.Root.new("chartContainer");
    let chart = root.container.children.push( 
      am5percent.PieChart.new(root, {
        layout: root.verticalLayout
      }) 
    );

    // Your dataPoints data
    let data = props.dataPoints;

    // Create series
    let series = chart.series.push(
      am5percent.PieSeries.new(root, {
        alignLabels: true,
				calculateAggregates: true,
        valueField: "y",
        categoryField: "name"
      })
    );
    series.slices.template.setAll({
      strokeWidth: 3,
      stroke: am5.color(0xffffff)
    });

    series.labelsContainer.set("paddingTop", 30)
    
    series.data.setAll(data);

    // Add legend
    let legend = chart.children.push(am5.Legend.new(root, {
      centerX: am5.percent(50),
      x: am5.percent(50),
      marginTop: 15,
			marginBottom: 15
    }));

    legend.data.setAll(series.dataItems);
    series.appear(1000, 100);

    return () => {
      root.dispose();
    };
  }, [props.dataPoints]); // Trigger effect when dataPoints change

  return (
    <div className="card-body pt-6">
      <div className="table-responsive" id='chartContainer' style={{ height: '370px', width: '100%', minHeight: "340px" }}></div>
      <div className="separator separator-dashed border-gray-200 mb-n4"></div>
    </div>
  );
}
export default PieChart;