import React, { useState, useEffect } from 'react';
import Loader from './Loader';

function Quote() {
  const [quote, setQuote] = useState(null);

  useEffect(() => {
    fetch('https://api.quotable.io/quotes/random?tag-name=motivation')
      .then(response => response.json())
      .then(data => {
        setQuote(data[0]);
      })
      .catch(error => console.error('Error fetching quote:', error));
  }, []);

  return (
    <div>
      <div className="fs-2qx fw-bold text-white mb-6">Quote of the day</div>
      {quote ? (
        <div>
          <span className="fw-bold text-white fs-5 mb-10 d-block opacity-90">
            {quote.content} - {quote.author}
          </span>
        </div>
      ) : (
        <div className="text-white"><Loader /></div>
      )}
    </div>
  );
}

export default Quote;