import React, { useState, useEffect } from 'react';
import Timeline from './Timeline';

const Stats = ({ data }) => {
    const [workedTime, setWorkedTime] = useState(0);
    const [productiveTime, setProductiveTime] = useState(0);
    const { checkedIn, checkedOut, totalBreaks, today } = data.userData;
    const [breakTime, setBreakTime] = useState(parseInt(totalBreaks?.total_break ?? 0));
    const { breakOn } = data;

    let workedIntervalId;
    let productiveIntervalId;
    useEffect(() => {
        
        if (checkedOut) {
            const startTime = checkedOut.time_worked;
            setWorkedTime(startTime);
            // No need to start interval if checked out
        } else if (checkedIn) {
            const startTime = checkedIn.time_worked;
            setWorkedTime(startTime);
            workedIntervalId = setInterval(() => {
                setWorkedTime(prevTime => prevTime + 1);
            }, 1000); // Update every second
        }

        return () => clearInterval(workedIntervalId);
    }, [checkedIn, checkedOut]);

    useEffect(() => {
       

        if (checkedOut) {
            const startTime = checkedOut.time_worked - totalBreaks.total_break;
            setProductiveTime(startTime);
            // No need to start interval if checked out
        } else if (checkedIn) {
            const startTime = checkedIn.time_worked - totalBreaks.total_break;
            setProductiveTime(startTime);
            productiveIntervalId = setInterval(() => {
                setProductiveTime(prevTime => prevTime + 1);
            }, 1000); // Update every second
        }

        return () => clearInterval(productiveIntervalId);
    }, [checkedIn, checkedOut, totalBreaks.total_break]);

    useEffect(() => {
        let breakIntervalId;
        if (totalBreaks) {
            const startTime = parseInt(totalBreaks?.total_break ?? 0);
            setBreakTime(startTime);
        }
        if (breakOn) {
            breakIntervalId = setInterval(() => {
                setBreakTime(prevTime => prevTime + 1);
            }, 1000); // Update every second
            clearInterval(workedIntervalId)
            clearInterval(productiveIntervalId)
        }

        return () => clearInterval(breakIntervalId);
    }, [breakOn, totalBreaks]);

    const convertSecondsToHoursMinutes = (seconds) => {
        const hours = Math.floor(seconds / 3600);
        const minutes = Math.floor((seconds % 3600) / 60);
        const remainingSeconds = Math.round(seconds % 60) ;
        return `${hours}h ${minutes}m ${remainingSeconds}s`;
    };

    return (
        <>
            <div className="col-md-4 mb-xxl-10">
                <div className="card card-reset mb-5 mb-xl-10">
                    <div className="card-body p-0">
                        <div className="row g-5 g-lg-9">
                            <div className="col-6">
                                <div className="card card-shadow">
                                    <div className="card-body p-0">
                                        <div className="btn btn-active-color-primary px-7 py-6 text-start w-100">
                                            <i className=" ki-rocket fs-2x text-gray-500 ms-n1">
                                                <div className="fs-2 fw-bolder" data-kt-countup="true" data-kt-countup-value="0" data-kt-countup-prefix="">
                                                    {convertSecondsToHoursMinutes(workedTime)}
                                                </div>
                                            </i>
                                            <div className="fw-bold fs-5 pt-4">
                                                Worked Today
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-6">
                                <div className="card card-shadow">
                                    <div className="card-body p-0">
                                        <div className="btn btn-active-color-primary px-7 py-6 text-start w-100">
                                            <i className=" fs-2 fw-bolder text-gray-500 ms-n1 time_break">
                                            {convertSecondsToHoursMinutes(breakTime)}
                                            </i>
                                            <div className="fw-bold fs-5 pt-4">Break</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-6">
                                <div className="card card-shadow">
                                    <div className="card-body p-0">
                                        <div className="btn btn-active-color-primary px-7 py-6 text-start w-100">
                                            <i className=" fs-2 fw-bolder text-gray-500 ms-n1">
                                                {(today) ? convertSecondsToHoursMinutes(today.total_time) : 0}
                                            </i>
                                            <div className="fw-bold fs-5 pt-4">Screenshots</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-6">
                                <div className="card card-shadow">
                                    <div className="card-body p-0">
                                        <div className="btn btn-active-color-primary px-7 py-6 text-start w-100">
                                            <i className=" ki-rocket fs-2x text-gray-500 ms-n1">
                                                <div className="fs-2 fw-bolder" data-kt-countup="true" data-kt-countup-value="0" data-kt-countup-prefix="">
                                                    {convertSecondsToHoursMinutes(productiveTime)}
                                                </div>
                                            </i>
                                            <div className="fw-bold fs-5 pt-4">Productive</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/* <div className="col-6">
                                <div className="card card-shadow">
                                    <div className="card-body p-0">
                                        <div className="btn btn-active-color-primary px-7 py-6 text-start w-100">
                                            <i className="ki-outline ki-chart-pie-3 fs-lg-2hx fs-2x text-gray-500 ms-n1"></i>
                                            <div className="fw-bold fs-5 pt-4">Subscriptions</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-6">
                                <div className="card card-shadow">
                                    <div className="card-body p-0">
                                        <div className="btn btn-active-color-primary px-7 py-6 text-start w-100">
                                            <i className="ki-outline ki-rescue fs-2x fs-lg-2hx text-gray-500 ms-n1"></i>
                                            <div className="fw-bold fs-5 pt-4">Help Center</div>
                                        </div>
                                    </div>
                                </div>
                            </div> */}
                        </div>
                    </div>
                </div>

                <Timeline />
            </div>
        </>
    );
};

export default Stats;